import "./Headers.css"
import {Link} from "react-router-dom";
import {useState} from "react";
import {Menu} from "../Menu/Menu";


const Header = ({clickBurger}) => {



    return (
        <>
            <div className="Header">
                <div className="header-left">
                    <Link to="/">
                        <img className="header-logo"
                             src="data:image/png;base64,"/>
                    </Link>
                </div>
                <div className="d-flex align-items-center justify-content-between">
                    <div className="header-right d-none d-sm-flex">
                        <Link to="/" className="header-content"></Link>
                        <Link to="/club" className="header-content"></Link>
                    </div>
                    <div className="menu-burger-btn d-flex d-sm-none" onClick={() => clickBurger()}><span/></div>

                </div>
            </div>

        </>
    )

}
export default Header

